import $http from "./http.js"
//
const common = {
    //  
    hotelInfo(obj) {
        return $http.get({
            url: "/mobile/" + obj.Suffix,
            params: obj.params,
            fn: obj.fn
        })
    },
}
//
const roomBooking = {
    //  
    activitylist(obj) {
        return $http.get({
            url: "/mobile/" + obj.Suffix,
            params: obj.params,
            fn: obj.fn
        })
    },
    //
    roomCategory(obj) {
        return $http.get({
            url: "/mobile/" + obj.Suffix,
            params: obj.params,
            fn: obj.fn
        })
    },
}


export {
    common,
    roomBooking
}


