'use strict'
import axios from 'axios'
import Qs from 'qs'
// import {
//     Message
// } from 'element-ui'
// axios https://api.douban.com/v2/movie/in_theaters
axios.defaults.baseURL = 'http://192.168.3.37:8080';
// axios.defaults.baseURL = 'http://192.168.3.47:8080';
//
axios.defaults.timeout = 5000;
//`withCredentails`
axios.defaults.withCredentials = false;
//
axios.defaults.headers = {
    'X-Requested-With': 'XMLHttpRequest',
    "Content-Type": "application/json"
};
//
axios.interceptors.request.use(config => {
    // tokenhttp headertoken
    if (sessionStorage.getItem('token') != undefined) {
        config.headers.token = sessionStorage.getItem('token');
    }
    return config
}, error => {
    return Promise.reject(error)
})

//
function checkStatus(response) {
    // 
    // Message.error(response.data.msg, '111111111');
    console.log(response.data.msg)
}
//
function checkCode(res) {
    // code(
    // Message.error('');
    // Message.error(res);
    // console.log(res.Error, res)
    console.log('')
}
export default {
    post(obj) {
        return axios.post(obj.url, obj.params).then(res => {
            if (res.data.code == 0) {
                if (obj.fn) {
                    obj.fn(res.data.data)
                }
            } else {
                return checkStatus(res)
            }
        }
        ).catch(err => {
            return checkCode(err)
        }
            )
    },
    get(obj) {
        return axios.get(obj.url, {
            params: obj.params
        }).then(res => {
            if (res.data.code == 0) {
                if (obj.fn) {
                    obj.fn(res.data.data)
                }
            } else {
                return checkStatus(res)
            }
        }
            ).catch(err => {
                return checkCode(err)
            }
            )
    },
    delete(obj) {
        return axios.delete(obj.url, {
            data: obj.params,
        }).then(res => {
            if (res.data.code == 0) {
                if (obj.fn) {
                    obj.fn(res.data.data)
                }
            } else {
                return checkStatus(res)
            }
        }
            ).catch(err => {
                return checkCode(err)
            }
            )
    },
}



